.wrapperEnlaces {
  grid-template-columns: 100%;
  grid-template-areas: "header"
                       "main"
                       "footer";
  gap: 60px;
  display: grid;
}

header {
  grid-area: header;
}

main {
  grid-area: main;
}

footer {
  grid-area: footer;
}

.containerEnlaces {
  width: 60%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

.title {
  text-align: center;
  margin-bottom: 15px;
  font-size: 50px;
}

.linkListTitle {
  line-height: 50px;
}

.linkList {
  color: #5d5f79;
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.linkListTitle {
  font-size: 20px;
  font-weight: 700;
  list-style-image: url("vinieta.8d3a78f5.jpg");
  list-style-position: outside;
}

.linkListElem {
  font-size: medium;
  font-weight: 400;
  line-height: 25px;
  list-style: none;
}

.listStyle {
  list-style-type: square;
}

.linkListElem:not(:first-of-type) {
  margin-top: 20px;
}

main a, main a:visited {
  color: #f59d23;
}

main a:hover {
  color: #5d5f79;
}

@media only screen and (max-width: 600px) {
  .wrapperEnlaces {
    gap: 30px;
  }

  .containerEnlaces {
    width: 80%;
  }

  .linkListTitle {
    font-size: 18px;
  }

  .linkListElem {
    font-size: 13px;
  }
}

